.card {
  width: 300px;
  height: 350px;
  border-radius: 10px;
  background: linear-gradient(135deg, #4dbb9f, #5cdc88);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.profile-picture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
}

.profile-picture img {
  width: 100%;
  height: auto;
  transform: scale(1.8);
}
