footer {
  background-color: #f5f5f5;
  padding: 10px 0;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.footer-logo img {
  max-width: 100%;
  height: auto;
}

.footer-links,
.footer-social,
.footer-contact {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* height: 100vh; */
}

.footer-links h3,
.footer-social h3,
.footer-contact h3 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
  text-align: center;
}

.footer-links ul li,
.footer-contact ul li {
  margin-bottom: 5px;
  list-style: none;
  text-align: center;
}

.footer-links a {
  text-decoration: none;
  color: #000;
}

.footer-social .social-icons {
  display: flex;
  flex-wrap: wrap;
}

.footer-social .social-icons a {
  display: inline-block;
  margin-right: 15px;
  margin-bottom: 15px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  transition: all 0.3s ease-in-out;
}

.footer-social .social-icons a:hover {
  transform: translateY(-2px);
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);
}

.footer-social .social-icons a svg {
  font-size: 20px;
  color: #000;
}

.footer-contact ul li span {
  margin-left: 10px;
  font-size: 14px;
}
