html,
body {
  height: 100%;
  margin: 0;
}

.container {
  display: flex;
  height: 100%;
}

.dummy-panel {
  width: 5%;
}

.side1 {
  width: 30%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #f6faff;
}

.brand img {
  margin-left: 30px;
  height: 60px;
}

.welcome-back {
  margin-left: 30px;
}

.welcome-back h1 {
  color: black;
}

.card-container {
  overflow-x: scroll;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;

  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.card-wrapper {
  display: flex;
  flex-direction: row;
}

.side2 {
  background-color: white;
  width: 70%;
  display: flex;
  align-items: center !important;
}

.login-form {
  max-width: 800px;
  padding: 20px;
  margin-right: auto;
}

.form-group {
  margin-top: 10px;
}

.btn-login {
  padding: 10px;
  border: none;

  margin-top: 20px;
  background-color: #4fc0a3;
  display: block;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  font-weight: semi-bold;
}

input {
  padding: 10px;
  border: 1px solid lightgrey;
  border-radius: 5px;
  width: 500px;
  margin-top: 10px;
  background-color: lightgrey;
}

.button-login button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  color: white;
}

.navigate {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* height: 100px; */
    padding: 10px;
    font-size:14px;
}
  
.inline-block {
    display: inline-block;
}
  
.left-container {
    width: 20%;
    text-align: left;
}

.right-container {
    width: 80%;
    text-align: right;
    color: grey;
}

.back-icon {
    margin-right: 5px;
    align-items: center;
}

.sign-up {
    text-decoration: underline;
    margin-left: 5px;
}

.forgot-password-container {
    text-align: right;
}

.forgot-password a {
    font-size: 12px;
    color: gray;
    text-decoration: none;
}

